module.exports = "\n    .button {\n        display: inline-block;\n        border-radius: 4px;\n        background-color: #117A65;/*  #a6c; */\n        border: none;\n        color: #FFFFFF;\n        text-align: center;\n        font-size: 14px;\n        /* padding: 5px; */\n        width: 100px;\n        height: 40px;\n        transition: all 0.5s;\n        cursor: pointer;\n        float: right;\n        margin: 5px;\n      }\n  \n\n.ng-valid[required], .ng-valid.required  {\n    border-left: 5px solid #42A948; \n  }\n  \n\n.ng-invalid:not(form)  {\n    border-left: 5px solid #a94442; \n  }\n  \n\n.ng-untouched:not(form) {\n    border-left: 5px solid rgb(14, 85, 151); \n}\n  \n\n.container{\n    /* height: 550px; */\n    width: 500px;\n    /* background-color: rgb(252, 249, 249); */\n    /* background-color: rgb(255, 253, 253); */\n    /* border-left: 1px solid; */\n    /* border-top: 1px solid;  */\n    padding: 10px;\n    /* box-shadow: 5px 10px 8px #888888; */\n}\n  \n\nlabel{\n    font-weight:bold;\n}\n  \n\n/* button:hover {\n    background-color: green;\n} */\n  \n\nbutton:disabled {\n    background-color: rgb(107, 151, 142);\n    /* color: white; */\n    cursor: not-allowed;\n}"