module.exports = ".button {\n    display: inline-block;\n    border-radius: 4px;\n    background-color: #117A65;/*  #a6c; */\n    border: none;\n    color: #FFFFFF;\n    text-align: center;\n    font-size: 14px;\n    /* padding: 5px; */\n    width: 100px;\n    height: 40px;\n    transition: all 0.5s;\n    cursor: pointer;\n    /* margin: 5px; */\n  }\n  \n  .button span {\n    cursor: pointer;\n    display: inline-block;\n    position: relative;\n    transition: 0.5s;\n  }\n  \n  .button span:after {\n    content: '\\00ab';\n    position: absolute;\n    opacity: 0;\n    top: 0;\n    left: -20px;\n    transition: 0.5s;\n  }\n  \n  .button:hover span {\n    padding-left: 25px;\n  }\n  \n  .button:hover span:after {\n    opacity: 1;\n    left: 0;\n  }"